}

.quizSubmit_progress {
    position: absolute;
    width: 100%;
    height: 100%;
    color: blue;
    background:-webkit-gradient( linear, left top, left bottom, color-stop(0.05, #33bdef), color-stop(1, #019ad2) );
    background-color: red;
}

/* progress bar animation */
.progressbar_value_blend {
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
  }

